import React, { useState } from 'react';
// import List from '../components/List'
import Layout from '../components/Layout'

const Index = (props) => {
	return <TodoCard />;
}

const TodoCard = () => {
	const [term, setTerm] = useState('');
	const [items, setItems] = useState(['cat', 'dog', 'bear', 'donkey'])
	
	const onChange = event => setTerm(event.target.value)

  const onSubmit = (event) => {
    event.preventDefault();
		if (term) {
			setTerm('')
			setItems([...items, term])
		}
	}
	
	return (
		<Layout>
		<div className="card">
		<div className="card-title">
	<h2>TODO LIST</h2>
		</div>
		<form className="" onSubmit={onSubmit}>
          <input placeholder="Enter todo" value={term} onChange={onChange} />
          <button><h2 className="btn-add">+</h2></button>
    </form>
				{/* <List items={items} /> */}

				<ul>
					{items.map((item,index) => {
								// 	return (
								// 		<li key={index}>
								// <div className="list-item">
								// <span>
								// {item}
								// </span>
								// 		<button onClick={props.items.splice(index,1)}>X</button>
								// </div>
								// 		</li>
										
								// 		)
					})}
				</ul>

		</div>
	</Layout>
	)
}


export default Index;